.workdays em { font-family: "FuturaPT-Demi", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 18px; letter-spacing: 0.1em; color: #CCC; font-style: normal; }

.complexity { display: inline-block; position: relative; width: 270px; height: 8px; box-sizing: border-box; border-radius: 4px; background: rgba(170, 170, 170, 0.1); overflow: hidden; transform: translate3d(0, 0, 0); }

.complexity__bar { content: ""; position: absolute; top: 0; left: 0; width: 100%; height: 100%; border-radius: 4px; background-color: #21DBB2; background-image: linear-gradient(20deg, #20d8a2 0%, #21e0c7 100%); transition: transform 1s ease-out; transform: translateX(-101%); }

.draw .complexity__bar { transform: translateX(0); }

.advantages { padding: 90px 0; min-height: 100vh; background: url("../img/advantages-bg.jpg") no-repeat center; background-size: cover; box-sizing: border-box; }

.advantages__title { margin-bottom: 60px; font-family: "FuturaPT-Demi", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 50px; letter-spacing: 1em; line-height: 1; text-transform: uppercase; }

.advantages__title small { display: block; text-decoration: none; font-family: "PFAgorasansPro-Regular", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 18px; line-height: 2.22; letter-spacing: 0; text-transform: none; color: #CCC; }

.slider { margin-top: 60px; white-space: nowrap; }

.slider-item { position: relative; display: inline-block; vertical-align: top; width: 380px; padding-right: 80px; font-size: 16px; box-sizing: border-box; white-space: normal; }

.slider-item__count { display: block; position: relative; font-family: "FuturaPT-Heavy", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 12px; letter-spacing: 0.1em; text-transform: uppercase; line-height: 1; margin-bottom: 30px; }

.slider-item__count:after { content: ""; display: block; margin-top: 30px; border-left: 1px solid; height: 110px; opacity: 0.3; }

.slider-item__title { display: block; font-family: "FuturaPT-Heavy", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 30px; letter-spacing: 0.1em; line-height: 1.2; color: inherit; text-decoration: none; text-transform: uppercase; }

.slider-item__text { margin: 40px 0 0; font-family: "PFAgorasansPro-Regular", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 18px; line-height: 1.22; }

.nomination { padding-top: 120px; position: relative; }

.nomination__footer { margin-top: 30px; max-width: 250px; padding-left: 90px; font-size: 18px; line-height: 1.333; }

.banner-title { position: relative; padding: 80px 90px; max-width: 830px; box-sizing: border-box; }

.banner-title__text { position: relative; z-index: 1; font-family: "FuturaPT-Demi", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 36px; letter-spacing: 0.1em; line-height: 1.39; color: #FFF; text-transform: uppercase; }

.banner-title__large { display: block; margin-top: 22px; font-family: "FuturaPT-Demi", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 72px; letter-spacing: 0.1em; line-height: 0.8; }

.causes { margin-top: 40px; }

.causes__header { text-align: right; margin-bottom: 140px; }

.causes__title { display: inline-block; vertical-align: top; text-align: left; font-family: "FuturaPT-Demi", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 48px; letter-spacing: 0.8em; line-height: 1.25; color: #777; text-transform: uppercase; }

.causes__title small { display: block; font-family: "PFAgorasansPro-Regular", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 18px; letter-spacing: 0; line-height: 2.22; text-transform: none; }

.causes__list { *zoom: 1; }

.causes__list:after { content: " "; display: table; clear: both; }

.cause { float: left; margin-bottom: 140px; width: 50%; box-sizing: border-box; position: relative; z-index: 1; padding: 0 120px 0 160px; }

.cause:nth-child(odd) { padding-right: 120px; float: left; }

.cause:nth-child(even) { float: right; padding-right: 100px; }

.cause:nth-child(2) { margin-top: 110px; }

.cause.-type-box .cause__text { color: inherit; }

.cause.-type-box:before { content: ""; position: absolute; z-index: -1; top: -60px; bottom: -60px; left: -95px; right: 0; background-color: #21DBB2; background-image: linear-gradient(20deg, #20d8a2 0%, #21e0c7 100%); }

.cause.-type-box:nth-child(odd):before { left: -1000px; }

.cause.-type-box:nth-child(even):before { right: -10000px; background: #424242; }

.cause.-color-dark { color: #333; }

.cause__count { display: block; position: absolute; top: 12px; left: 0; font-family: "FuturaPT-Demi", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 12px; letter-spacing: 0.1em; line-height: 1; white-space: nowrap; }

.cause__count:after { content: ""; display: inline-block; margin-left: 25px; vertical-align: middle; position: relative; top: -2px; width: 70px; border-top: 1px solid; opacity: 0.3; }

.cause__title { display: block; margin-bottom: 30px; font-family: "FuturaPT-Demi", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 30px; letter-spacing: 0.1em; line-height: 1.2; text-transform: uppercase; }

.cause__text { color: #777; }

.header { position: relative; z-index: 2; line-height: 50px; box-sizing: border-box; padding: 50px 20px 50px 160px; max-width: 1180px; margin: auto; text-align: left; *zoom: 1; }

.header:after { content: " "; display: table; clear: both; }

.header__logo { position: absolute; top: 35px; left: 20px; }

.header__nav { float: right; }

.header__panel { background: #2F2F2F; color: #CCC; float: right; padding: 0 35px 0 40px; margin-left: 40px; }

.header__panel .phone-number:not(:first-child) { margin-left: 35px; }

.nav__item { display: inline-block; vertical-align: top; }

.nav__item:not(:last-child) { margin-right: 25px; }

.nav__link { display: inline-block; vertical-align: top; font-family: "PFAgorasansPro-Medium", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 12px; letter-spacing: 0.1em; color: #FFF; text-transform: uppercase; text-decoration: none; transition: color 0.2s ease; }

.nav__link:hover { color: #21DBB2; }

.logo { display: block; position: relative; width: 100%; width: 135px; height: 60px; box-sizing: border-box; padding: 27px 3px 3px 12px; font-family: "FuturaPT-Demi", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 24px; line-height: 1; text-decoration: none; color: #FFF; }

.logo:after { content: " "; display: block; margin-top: 15px; height: 12px; width: 110px; background: url("../img/svg/startup.svg") no-repeat center; background-size: contain; }

.phone-number { display: inline-block; font-family: "FuturaPT-Demi", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 14px; letter-spacing: 0.1em; text-decoration: none; color: inherit; }
